import React from "react";
import "./Content.css";

const Content = () => {
  const FeatureItem = ({ iconClass, title, description }) => {
    const [hovered, setHovered] = React.useState(false);

    return (
      <div
        className={`featureItem ${hovered ? "featureItemHover" : ""}`}
        onMouseEnter={() => setHovered(true)}
        onMouseLeave={() => setHovered(false)}
      >
        <i className={`${iconClass} icon`}></i>
        <div>
          <h3 className="featureTitle">{title}</h3>
          <p className="paragraph">{description}</p>
        </div>
      </div>
    );
  };

  return (
    <div className="container-main">
      {/* Features Section */}
      <h2 className="heading">Features of the Random YouTube Video Player</h2>
      <div className="featuresContainer">
        <FeatureItem
          title="Effortless Video Selection"
          description="Skip the hassle of browsing endlessly. This tool picks videos for you based on your favorite topics."
        />
        <FeatureItem
          title="Learn English by Subject"
          description="Dive into curated videos to improve your vocabulary while exploring topics like science, music, or comedy."
        />
        <FeatureItem
          title="Ad-Free Experience"
          description="Enjoy uninterrupted learning without distractions."
        />
      </div>

      {/* What Is Section */}
      <h2 className="heading">What Is the Random YouTube Video Player?</h2>
      <p className="paragraph">
        The Random YouTube Video Player is a tool designed to{" "}
        <span className="highlight">combine entertainment with learning</span>. It curates videos based on your interests to make learning English fun and seamless.
      </p>

      {/* Conclusion Section */}
      <h2 className="heading">Conclusion</h2>
      <p className="paragraph">
        This tool is your personalized gateway to{" "}
        <span className="highlight">learning English in an engaging way</span>. Explore, learn, and entertain yourself without the browsing fatigue!
      </p>

      {/* FAQs Section */}
      <h2 className="heading">FAQs</h2>
      <div className="faqItem">
        <p className="question">
          1. How does the Random YouTube Video Player work?
        </p>
        <p className="answer">
          The player selects random YouTube videos based on your chosen subject. It’s
          designed to curate content for effortless learning and entertainment.
        </p>
      </div>
      <div className="faqItem">
        <p className="question">
          2. Can I choose a specific subject?
        </p>
        <p className="answer">
          Yes, select topics like science, music, or comedy to tailor the videos to
          your preferences.
        </p>
      </div>
      <div className="faqItem">
        <p className="question">
          3. Is this tool free to use?
        </p>
        <p className="answer">
          Yes, it’s completely free and{" "}
          <span className="highlight">ad-free</span> for an uninterrupted experience.
        </p>
      </div>
    </div>
  );
};

export default Content;
