import React, { useRef, useState, useEffect } from "react";
import "./YouTubeApp.css";
import BuyMeACoffeeButton from './BuyMeACoffeeButton'
import YoutubeVideo from "youtube-video-element/react";
import MediaThemeSutro from "player.style/sutro/react";
import ScrollToTopButton from "./ScrollToTopButton";
import Content from "./Content";

function YouTubeApp() {
  const [videoId, setVideoId] = useState("");
  const [videoIds, setVideoIds] = useState([]);
  const [loading, setLoading] = useState(true); // Track loading status
  const [canSwitch, setCanSwitch] = useState(true);
  const [videoHistory, setVideoHistory] = useState([]); // Tracks history of played videos
  const [currentIndex, setCurrentIndex] = useState(-1); // Index in the video history

  const [files, setFiles] = useState([]);
  const [selectedFile, setSelectedFile] = useState("");

  const [isMobile, setIsMobile] = useState(false);
  const [menuVisible, setMenuVisible] = useState(false);

  const videoRef = useRef(null);

  const [isDarkMode, setIsDarkMode] = useState(false);

  // Function to generate a random color in RGB format
  const getRandomColor = () => {
    const r = Math.floor(Math.random() * 256);
    const g = Math.floor(Math.random() * 256);
    const b = Math.floor(Math.random() * 256);
    return `rgb(${r}, ${g}, ${b})`;
  };

  const [gradientState, setGradientState] = useState({
    x: 0,
    y: 0,
    color1: getRandomColor(),
    color2: getRandomColor(),
  });
  const timeoutRef = useRef(null); // Reference to store the timeout ID

  const delay = 30; // Delay in milliseconds (e.g., 30ms)

  const handleMouseMove = (e) => {
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current); // Clear the previous timeout
    }

    timeoutRef.current = setTimeout(() => {
      const x = e.clientX;
      const y = e.clientY;

      // Update both the gradient position and random colors
      setGradientState({
        x,
        y,
        color1: getRandomColor(),
        color2: getRandomColor(),
      });
    }, delay); // Delay the handling of the event
  };

  useEffect(() => {
    // Attach event listener to the document
    document.addEventListener("mousemove", handleMouseMove);

    // Cleanup the event listener on component unmount
    return () => {
      document.removeEventListener("mousemove", handleMouseMove);
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current); // Clear any active timeout when unmounting
      }
    };
  }, []);
  const toggleDarkMode = () => {
    setIsDarkMode((prevMode) => !prevMode);
  };

  // Add/remove event listener for the checkbox
  useEffect(() => {
    const checkbox = document.getElementById("checkbox");
    const handleChange = () => {
      document.body.classList.toggle("dark");
      toggleDarkMode();
    };

    checkbox.addEventListener("change", handleChange);

    // Cleanup the event listener on unmount
    return () => {
      checkbox.removeEventListener("change", handleChange);
    };
  }, []);

  // Apply theme changes to specific elements when `isDarkMode` changes
  useEffect(() => {
    const applyTheme = (element, isDark) => {
      if (element) {
        element.classList.toggle("dark-mode", isDark);
        element.classList.toggle("light-mode", !isDark);
      }
    };

    const app = document.querySelector(".App");

    applyTheme(app, isDarkMode);
  }, [isDarkMode]);

  const handleVideoEnd = () => {
    handleNext();
  };

  useEffect(() => {
    // Function to check if the screen is mobile
    const checkMobile = () => {
      const isMobileDevice =
        /Mobi|Android|iPhone|iPad|iPod/i.test(navigator.userAgent) ||
        window.innerWidth <= 768;
      setIsMobile(isMobileDevice);
    };

    checkMobile(); // Initial check on load
    window.addEventListener("resize", checkMobile); // Add resize event listener

    return () => {
      window.removeEventListener("resize", checkMobile); // Cleanup on unmount
    };
  }, []);

  const toggleMenu = () => {
    setMenuVisible(!menuVisible);
  };

  useEffect(() => {
    const lastSelected = localStorage.getItem("lastSelectedFile");
    const favoriteList = JSON.parse(localStorage.getItem("favoriteList")) || [];

    // Check if lastSelected is valid
    if (lastSelected && files.includes(lastSelected)) {
      setSelectedFile(lastSelected);
    } else if (favoriteList.length > 0) {
      // If "My Favourite" exists and there's data in the favorite list, use it as default
      setSelectedFile("favourites");
      setVideoIds(favoriteList);
      getRandomVideoId();
    } else {
      if (files.length > 0) setSelectedFile(files[0]);
      //console.log("First time here");
    }
  }, [files]);

  useEffect(() => {
    // Fetch the list of JSON files from the public folder
    fetch("/json/fileIndex.json")
      .then((response) => {
        if (!response.ok) {
          throw new Error("Failed to fetch file index.");
        }
        return response.json();
      })
      .then((data) => {
        // Check if data is empty
        if (Array.isArray(data) && data.length === 0) {
          // Handle the case when fileIndex.json is empty
          console.log("fileIndex.json is empty. Setting a default value.");
          setFiles([]); // Set an empty array or any fallback value
        } else {
          // If data is not empty, set the files state
          setFiles(data);
        }
      })
      .catch((error) => console.error("Error loading file index:", error));
  }, []);

  // Fetch video IDs based on the selected JSON file
  useEffect(() => {
    if (selectedFile === "") {
      return;
    }
    if (selectedFile === "favourites") {
      setLoading(false);
      return;
    }
    setLoading(true); // Start loading
    fetch(`/json/${selectedFile}`)
      .then((response) => {
        if (!response.ok) {
          throw new Error("Failed to load video IDs");
        }
        return response.json();
      })
      .then((data) => {
        setVideoIds(data);
        if (data.length > 0) {
          // Set a random video ID initially
          const randomIndex = Math.floor(Math.random() * data.length);
          const initialVideoId = data[randomIndex];
          setVideoId(initialVideoId);
          setVideoHistory([initialVideoId]); // Initialize history
          setCurrentIndex(0); // First video in history
        }
        setLoading(false); // Stop loading
      })
      .catch((error) => {
        console.error("Error fetching video IDs:", error);
        setLoading(false); // Stop loading even if there's an error
      });
  }, [selectedFile]); // Re-fetch when the selectedFile changes

  const getNextVideoId = (videoIds) => {
    const ignoreList = getLocalStorageList("ignoreList");

    if (videoIds.length === 0) {
      console.error("No videos available in the list!");
      return null;
    }

    let newVideoId;
    let attempts = 0;
    const maxAttempts = videoIds.length * 2; // Prevent infinite loops in case all videos are ignored

    do {
      const randomIndex = Math.floor(Math.random() * videoIds.length);
      newVideoId = videoIds[randomIndex];
      attempts++;

      // Break loop if we've exceeded max attempts to avoid infinite loops
      if (attempts >= maxAttempts) {
        console.warn(
          "All videos seem to be in the ignore list or no valid video found."
        );
        return null;
      }
    } while (ignoreList.includes(newVideoId));

    return newVideoId;
  };

  // Handle random video selection
  const getRandomVideoId = () => {
    if (!canSwitch || videoIds.length === 0) return; // Prevent rapid switching
    setCanSwitch(false); // Temporarily disable switching
    setVideoId(""); // Reset videoId to show loading
    setTimeout(() => {
      const nextVideoId = getNextVideoId(videoIds); // videoIds is your array of available video IDs
      if (nextVideoId) {
        setVideoId(nextVideoId);

        // Update history
        const updatedHistory = [
          ...videoHistory.slice(0, currentIndex + 1),
          nextVideoId,
        ];
        setVideoHistory(updatedHistory);
        setCurrentIndex(updatedHistory.length - 1); // Update to latest index
        setCanSwitch(true);
      } else {
        console.error("No valid video ID found to play.");
      }
    }, 100); // delay some
  };

  const setHistoryVideoId = (index) => {
    if (!canSwitch || videoIds.length === 0) return; // Prevent rapid switching
    setCanSwitch(false); // Temporarily disable switching
    setVideoId(""); // Reset videoId to show loading
    setVideoId(videoHistory[index]);
    setCurrentIndex(index);
    setCanSwitch(true);
  };

  // Handle previous video
  const handlePrevious = () => {
    if (currentIndex > 0) {
      const previousIndex = currentIndex - 1;
      setHistoryVideoId(previousIndex);
    }
  };

  // Handle next video
  const handleNext = () => {
    if (currentIndex < videoHistory.length - 1) {
      // If there's a next video in the history
      const nextIndex = currentIndex + 1;
      setHistoryVideoId(nextIndex);
    } else {
      // If no future video in history, pick a random one
      getRandomVideoId();
    }
  };

  // Handle Space key press to trigger random button
  useEffect(() => {
    const handleKeyDown = (event) => {
      event.preventDefault(); // Prevent default space behavior (e.g., scrolling)
      console.log("Key pressed:", event.code);

      switch (event.code) {
        case "ArrowRight": // Go to the next video
          handleNext();
          break;
        case "ArrowLeft": // Go to the previous video
          handlePrevious();
          break;
        case "Space": // Toggle play/pause
          if (videoRef.current) {
            const videoElement = videoRef.current;
            if (videoElement.paused) {
              videoElement.play();
            } else {
              videoElement.pause();
            }
          }
          break;
        default:
          break;
      }
    };

    window.addEventListener("keydown", handleKeyDown);

    // Cleanup event listener on component unmount
    return () => {
      window.removeEventListener("keydown", handleKeyDown);
    };
  }, [videoIds, canSwitch]);

  const handleSearch = () => {
    getRandomVideoId();
  };

  // Function to get a list from localStorage or return an empty array if not found
  const getLocalStorageList = (key) => {
    const storedList = localStorage.getItem(key);
    return storedList ? JSON.parse(storedList) : [];
  };

  // Function to save a list to localStorage
  const setLocalStorageList = (key, list) => {
    localStorage.setItem(key, JSON.stringify(list));
  };

  const handleLike = () => {
    const favoriteList = getLocalStorageList("favoriteList");

    if (!favoriteList.includes(videoId)) {
      favoriteList.push(videoId);
      setLocalStorageList("favoriteList", favoriteList);
      setVideoIds(favoriteList);
    }
  };

  const handleDislike = () => {
    const favoriteList = JSON.parse(localStorage.getItem("favoriteList")) || []; // Get the favorite list from localStorage
    // Check if the videoId is in the favorite list
    const updatedFavoriteList = favoriteList.filter((id) => id !== videoId);
    // Update the favorite list in localStorage
    localStorage.setItem("favoriteList", JSON.stringify(updatedFavoriteList));
    if (updatedFavoriteList.length === 0) {
      if (files.length > 0) setSelectedFile(files[0]);
    }

    const ignoreList = getLocalStorageList("ignoreList");
    if (!ignoreList.includes(videoId)) {
      ignoreList.push(videoId);
      setLocalStorageList("ignoreList", ignoreList);
    }
  };

  // Style for the button, using gradientPosition and random colors
  const buttonStyle = {
    background: `radial-gradient(circle at ${gradientState.x}px ${gradientState.y}px, ${gradientState.color1}, ${gradientState.color2})`,
    transition: "background 0.5s ease",
  };

  return (
    <div className="main-layout">
      {/* Scroll to Top Button */}
      <ScrollToTopButton />
      <div className="blank-layout"></div>
      <div className="top-layout">
        <div className="search-container">
          <a title="Z Main Page" href="/" className="logo-link">
            <img
              src="/icon.png"
              width="50"
              height="50"
              alt="Logo"
              className="logo"
            />
          </a>
          {!isMobile && (
            <button
              title="Random"
              className="random-button"
              onClick={handleSearch}
              style={buttonStyle}
            >
              Random
            </button>
          )}
          <label htmlFor="fileSelector" className="sunset-label">
            Select a subject:
          </label>
          <select
            id="fileSelector"
            value={selectedFile}
            onChange={(e) => {
              const selectedValue = e.target.value;
              // Save the selected value to localStorage
              localStorage.setItem("lastSelectedFile", selectedValue);
              if (selectedValue === "favourites") {
                const favoriteList = getLocalStorageList("favoriteList");
                if (favoriteList.length > 0) {
                  // Set a random video ID initially
                  const randomIndex = Math.floor(
                    Math.random() * favoriteList.length
                  );
                  const initialVideoId = favoriteList[randomIndex];
                  setVideoId(initialVideoId);
                  setVideoHistory([initialVideoId]); // Initialize history
                  setCurrentIndex(0); // First video in history
                }
                setSelectedFile(selectedValue);
              } else {
                setSelectedFile(selectedValue);
              }
            }}
            className="sunset-select"
          >
            {/* Conditionally render "My Favourite" only if the favoriteList is not empty */}
            {getLocalStorageList("favoriteList").length > 0 && (
              <option value="favourites">My Favourite</option>
            )}
            {files.map((file, index) => (
              <option key={index} value={file}>
                {file.replace("videoIds-", "").replace(".json", "") || "All"}
              </option>
            ))}
          </select>
          <input type="checkbox" className="checkbox" id="checkbox" />
          <label htmlFor="checkbox" className="checkbox-label">
            <i className="icon-moon"></i>
            <i className="icon-sun"></i>
            <span className="ball"></span>
          </label>
        </div>
      </div>
      <div className="webview-layout">
        {loading || videoId === "" || files.length === 0 ? (
          <div className="loading-container">
            <div className="loading-spinner"></div>
            {files.length === 0 ? (
              <div>No files found. Please check your file index.</div>
            ) : loading ? (
              <div>Loading video IDs...</div>
            ) : (
              <div>Wait about a little...</div>
            )}
          </div>
        ) : (
          <>
            <MediaThemeSutro>
              <YoutubeVideo
                autoplay
                slot="media"
                src={`https://www.youtube.com/watch?v=${videoId}`}
                playsInline
                crossOrigin
                ref={videoRef}
                onEnded={handleVideoEnd}
              ></YoutubeVideo>
            </MediaThemeSutro>
          </>
        )}
      </div>
      {/* Buttons Container */}
      <div className="button-container">
        <button
          title="Previous"
          className="button-custom previous-button"
          onClick={handlePrevious}
          disabled={currentIndex <= 0}
        >
          <span className="iconsized">◀</span>
        </button>
        <button
          title="Like"
          className="button-custom like-button"
          onClick={handleLike}
        >
          <span className="iconsized">❤️</span>
        </button>
        <button
          title="Dislike"
          className="button-custom dislike-button"
          onClick={handleDislike}
        >
          <span className="iconsized">🖤</span>
        </button>
        <button
          title="Next"
          className="button-custom next-button"
          onClick={handleNext}
          disabled={
            currentIndex >= videoHistory.length - 1 && videoIds.length === 0
          }
        >
          <span className="iconsized">▶</span>
        </button>
      </div>
      {isMobile && (
        <>
          {/* Render the RANDOM button */}
          <button
            title="Random"
            className="random-button"
            onClick={handleSearch}
            style={buttonStyle}
          >
            Random
          </button>
        </>
      )}
      <Content />
      <BuyMeACoffeeButton />
      <div className="ad ad-left"></div>
      <div className="ad ad-right"></div>
    </div>
  );
}

export default YouTubeApp;
